* {
  box-sizing: border-box;
}

body {
  background: var(--background);
  margin: 0;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

:root {
  --background: #535e44;
  --titleBackground: #190c7a;
  --textColor: #fff;
  --subTextColor: #c2c7d1;
  --boxBackground: #acbe94;
  --topicBackground: #2b19b3;
  --formulaBackground: #7d1f69;
}

body > h1 {
  background: var(--titleBackground);
  text-align: center;
  color: var(--textColor);
  letter-spacing: 2px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 6vh;
  margin: 0;
  display: flex;
}

.box {
  background: var(--boxBackground);
  border-radius: 10px;
  width: min(1000px, 60%);
  margin: 2% auto;
  box-shadow: 1px -1px 4px #000;
}

.box > h1, .questions > h1 {
  background: var(--topicBackground);
  text-align: center;
  color: var(--textColor);
  border-radius: 8px 8px 0 0;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 5px;
  font-size: 1.6rem;
  font-weight: 400;
  display: flex;
}

.content {
  padding: 10px 20px;
}

.box .content > h1 {
  text-align: center;
  border-bottom: 1px solid #000;
  font-size: 1.8rem;
  font-weight: 400;
}

.box .content div {
  flex-flow: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.box .content div span {
  cursor: default;
  background: var(--formulaBackground);
  text-align: center;
  color: var(--textColor);
  margin: 5px 0;
  padding: 10px;
}

.box .content ul {
  border: 1px solid #000;
  padding: 10px 10px 10px 40px;
}

.box .content ul li {
  margin: 10px 0;
  font-size: 1.5rem;
}

.box .content canvas {
  cursor: pointer;
  margin: 10px auto;
  display: block;
}

.box .content img {
  margin: 10px auto;
  display: block;
}

.questions {
  background: var(--titleBackground);
  border-radius: 10px;
  width: min(1000px, 60%);
  margin: 4% auto;
  box-shadow: 1px -1px 4px #000;
}

.questions h2 {
  color: var(--textColor);
  border-bottom: 1px var(--subTextColor) solid;
  font-weight: 400;
}

.questions .content > div {
  grid-gap: 4px;
  grid-template-columns: 1fr 1fr;
  display: grid;
}

.questions .content > div .side {
  background: var(--topicBackground);
  padding: 10px;
}

.questions .content > div .resolution {
  opacity: 0;
  transition: all .5s;
  transform: scale(0);
}

.questions p {
  color: var(--subTextColor);
}

.questions label {
  color: var(--subTextColor);
  justify-content: space-evenly;
  margin: 15px auto;
  display: flex;
}

.questions input {
  text-align: center;
  background: var(--formulaBackground);
  color: var(--textColor);
  border: solid 1px var(--textColor);
  width: auto;
}

.questions input.correct {
  background: green;
}

.questions button {
  margin: auto;
  display: block;
}

.footer {
  color: var(--textColor);
  text-align: center;
  width: 100%;
  height: 100px;
}

@media screen and (width <= 600px) {
  .box, .questions {
    width: calc(100% - 2vmin);
  }
}

@media screen and (width <= 800px) {
  .questions .content {
    padding: 10px;
  }

  .questions label {
    flex-flow: column;
    align-items: center;
  }

  .questions input {
    width: min(100%, 200px);
    margin: 15px 0;
  }

  .questions .content > div {
    grid-gap: 4px;
    grid-template-columns: unset;
    grid-template-rows: 1fr 1fr;
    display: grid;
  }
}

